nav {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
	margin: 0px;
	padding: 0px;
	background-color: #5e8fba;
	color: #F7F7F7;
}
.title {
	margin-left: 3vw;
	font-weight: bold;
	font-size: 2em;
	margin-right: auto;
}
#buttons {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	margin-right: 10%;
	margin-left: auto;
}
a{
	display: block;
	width:8vw;
	padding: 1vw;
	margin: 1vw 2vw;
	border: 1px solid white;
	border-radius: 5px;
	color: white;
}
a:hover {
	cursor: pointer;
	background-color: rgba(52, 108, 156, 1);
}
.dropdown {
    position: relative;
    display: inline-block;
}
.dropdown-content{
	display: none;
    position: absolute;
    -webkit-transform: translate(20%);
        -ms-transform: translate(20%);
            transform: translate(20%);
    background-color: rgba(52, 108, 156, 0.8);
    min-width: 80%;
    -webkit-box-shadow: 0px 2vw 16px 0px rgba(0,0,0,0.2);
            box-shadow: 0px 2vw 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}
.dropdown-content a {
	display: block;
	border: none;
	border-radius: 0;
	margin: 0;
	padding: 1vw 0;
	text-align: center;
	width: 100%;
}
.expanded {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column nowrap;
	    flex-flow: column nowrap;
	-ms-flex-align: center;
	    align-items: center;
}
.dropbtn {
	display: -ms-inline-flexbox;
	display: inline-flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	min-width: 7em;
}
#NewGame{
	text-align: center;
	margin-right: 10%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	display: inline-block;
	min-width: 7em;
}


